import React, { Component } from "react";
import moment from "moment";

class Fecha extends Component {
	render() {
		moment.locale("es", {
			months: [
				"Enero",
				"Febrero",
				"Marzo",
				"Abril",
				"Mayo",
				"Junio",
				"Julio",
				"Agosto",
				"Septiembre",
				"Octubre",
				"Noviembre",
				"Diciembre"
			],
			weekdays: [
				"Lunes",
				"Martes",
				"Mircoles",
				"Jueves",
				"Viernes",
				"Sbado",
				"Domingo"
			]
		});
		const date = moment(this.props.date).format("DD [de] MMMM, YYYY");
		return <span>{date}</span>;
	}
}

export default Fecha;



